.wp-block-gutenberg-glosasrio-block {
  border: 0.5px solid rgba(0, 0, 0, 0.1);
  background-color: white;
  box-shadow: 2px 8px 17px #dedede;
  margin: 0 auto;
  margin-top: 25px;
  padding: 30px 40px;
}
.wp-block-gutenberg-glosasrio-block h3 {
  font-size: 23px;
  line-height: 1.4;
  font-weight: 500;
  margin: 0px !important;
}
.wp-block-gutenberg-glosasrio-block p {
  margin: 0 !important;
  margin-top: 15px !important;
}
.wp-block-gutenberg-glosasrio-block p:first-child {
  margin-top: 10px !important;
}